<template>
	<v-app >
		<div>
			<v-container  fluid>
				<v-layout align-center justify-center>
					<v-flex xs12 sm12 md10>
						<v-card class="mx-auto">
							<v-row no-gutters>
								<v-col cols="12">
									<div>
										<div class="ml-4 mr-4">
											<h3 class="mt-6 ml-5 mb-5 text-center">Missed calls</h3>
										</div>
										<h3 class="mt-2 ml-3">Routing method - New caller</h3>
											<v-radio-group v-model="radios1" mandatory  class="ml-3">
												<v-radio label="Assign to all" value="Assign_to_all"></v-radio>
												<v-radio label="Assign to Specific Agent(s)" value="Assign_to_specific" ></v-radio>
												<div no-action v-if="radios1 == 'Assign_to_specific'">

<!-- 
  <v-list-tile v-for="(seleted_users, index) in items" :key="seleted_users.Name">
    <v-list-tile-content>
      <v-checkbox :key="seleted_users.Name" :label="seleted_users.Name" :value="seleted_users.Name" v-model="checkboxes[index].checked">
      </v-checkbox>
    </v-list-tile-content>
  </v-list-tile>
 -->

													<div  :id="element.Name" v-for="element in seleted_users" :key="element.Name">
														<v-checkbox
															v-model="getMissed"
															value="element.Name"
															
														></v-checkbox>
														{{element.Name}}
													</div>

												</div>
												<v-radio label="Assign in Round Robin" value="Assign_to_round_robin"></v-radio>
												<v-radio label="Assign to last Attempted" value="Assign_to_last" ></v-radio>
											</v-radio-group>


										<h3 class="mt-2 ml-3">Routing method - Repeated caller</h3>
										<v-checkbox v-model="selected" label="Sticky Agent" ></v-checkbox>
									</div>
									<v-btn class="ml-4" color="primary" @click="applyRouting()">Apply Routing</v-btn>
								</v-col>
							</v-row>
							<br>
							<br>
						</v-card>
					</v-flex>
				</v-layout>
			</v-container>
		</div>
	</v-app>
</template>


<script>
// import Vue from 'vue'
// import axios from 'axios'
import firebase from 'firebase'
import { db } from '@/main.js';
  export default {

    data: () => ({
			uid : '',
			phno : '',
			name: '',
			virtual_number : '',
      role :'',
      detail: {},
      participants: {},
      added_list_json : {},
			Udata : [],
			realdata : [],
			all_participants : [],
			user_listing : [],
			added_list: [],
			seleted_users : [],
			toggle_none : null,
			userUid : '',
			ownerid : '',
			radios1: null,
			radios2: null,
			selected : false,
			getMissed : [],
			checkboxes: [],
    }),
		methods: {
			applyRouting() {
				if (this.selected){
					this.radios2 = 'sticky-agent'
				}
				else {
					this.radios2 = null
				}
				const user_data = {
				url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/callDistribution/missedcall',
				method: 'POST',
				data: {
					owner_uid: this.uid,
					updated_by: this.uid,
					virtual_number : this.virtual_number,
					source : 'general',
					participants: this.seleted_users,
					new_missed_caller : this.radios1,
					repeated_missed_caller : this.radios2
				},
			}
			console.log(user_data)
			this.$axios(user_data)
				.then((response) => {
					console.log(response)
				})
				.catch((error) => {
					console.error(error);
				})
			}

  },
		created() {
			firebase.auth().onAuthStateChanged(user => {
				if (user) {
					console.log("logged user details",user)
					this.uid = user.uid
					// this.phno = user.phoneNumber.slice(3)
					console.log("page Managers il user id", this.uid)
					// console.log("page Managers il user number", this.phno)
					db.collection('users').where("uid" , "==" , this.uid).get().then((querySnapshot) => {
						querySnapshot.forEach((doc) => {
							console.log(doc.id, " => ", doc.data());
							let user_details = doc.data()
							this.ownerid = user_details.OwnerUid
							this.uid = user_details.uid
							this.virtual_number = user_details.virtualNumber
							console.log(user_details.role)
							console.log(this.uid)


							db.collection('uservirtualNumber').where("Uid" , "==" , this.uid).get().then((querySnapshot) => {
								querySnapshot.forEach((doc) => {
									console.log(doc.id, " => ", doc.data());
									let user_details = doc.data()
									this.Udata = user_details
									var trying = this.Udata.Participants
									this.radios1 = this.Udata.NewMissedCaller
									this.radios2 = this.Udata.RepeatedMissedCaller
									if (this.radios2) {
										this.selected = true
									}
									trying.forEach((items) => {
										console.log("loop items",items)
										this.added_list_json = Object.assign({}, this.added_list_json, { Number: items.Number , Name: items.Name , AgentUid : items.AgentUid})
										this.added_list.push(this.added_list_json)
										this.seleted_users.push(this.added_list_json)
									})
									console.log('im getting',this.added_list)
									
									console.log('before data ', this.seleted_users)

										
									})
								})
								.catch((error) => {
									console.log("Error getting documents: ", error);
								})

							})
					})
				}
			})
		}
  }

</script>